import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Resposta_campo_formulario } from '../../../modelo/Resposta_campo_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resposta_campo_formulario-dados-details',   templateUrl: 'resposta_campo_formulario_dadosDetails.component.html' }) export class Resposta_campo_formularioDadosDetailsComponent implements OnInit {    resposta_campo_formulario: Resposta_campo_formulario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.resposta_campo_formulario.Resposta_campo_formulario";                 let id = +params['id'];         this.resposta_campo_formulario = new Resposta_campo_formulario();         this.resposta_campo_formulario.cd_resposta_campo_formulario = id;         o.objetoJson = this.resposta_campo_formulario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a resposta_campo_formulario. '; }      this.resposta_campo_formulario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


